import i18next from 'i18next'
import Backend from 'i18next-fs-backend'
import path from 'path'

import { i18nConfig, Locale } from './config'

const initialized = new Map<string, boolean>()

export async function getServerTranslation(locale: string, ns = 'common') {
  console.log(locale, 'locale')
  if (!i18nConfig.locales.includes(locale as Locale)) {
    locale = i18nConfig.defaultLocale
  }

  const key = `${locale}-${ns}`
  if (!initialized.get(key)) {
    await i18next.use(Backend).init({
      lng: locale,
      ns: [ns],
      fallbackLng: i18nConfig.defaultLocale,
      backend: {
        loadPath: path.resolve(process.cwd(), `apps/client/lib/i18n/locales/{{lng}}/{{ns}}.json`)
      },
      initImmediate: false
    })
    initialized.set(key, true)
  }

  return (k: string) => i18next.t(k)
}
